<template>
    <section class="container lease-car">
        <v-breadnav>惠搜车</v-breadnav>
        <v-searchbar @getOption = "getselected"></v-searchbar>
        <v-list :productArr = "listArr" :type = '1'></v-list>
        <v-pageNation :totalcount = "totalCount" @getPage = "getPage"></v-pageNation>
    </section>
</template>

<script>
import vBreadnav from "@/components/pc/breadNav"
import vSearchbar from "@/components/pc/searchOption"
import vList from "@/components/pc/carlist"
import vPageNation from "@/components/pc/pageNation"
import {PC_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data:function(){
        return {
            listArr:[],
            totalCount:0,
            selectOption:{},
            carId:this.$route.params.id,
            price:this.$route.params.price
        }
    },
    methods:{
        getselected:function(data){
            this.selectOption = data;
            this.createJson();
        },
        getPage:function(data){        
            let json = this.createJson(data);
        },
        createJson:function(page){
            let data = new Object();
            let options = $.extend({},this.selectOption);           
            if(Object.keys(options).length != 0){
                if(options.pageNum){
                data.pageNum  = page;
                }else{
                    data.pageNum = 1;
                }           
                data.pageSize = 10;
                data.brandId = options.brandId ==""? null : options.brandId ;
                data.brandSeriesId = options.carId == "" ? null : options.carId;
                if(options.firstPayNum == null){
                    data.fromFirstPayment = null;
                    data.toFirstPayment = null;
                }else{
                    if(options.firstPayNum.indexOf('-')!=-1){
                        data.fromFirstPayment = options.firstPayNum.split('-')[0];
                        data.toFirstPayment = options.firstPayNum.split('-')[1]
                    }else{
                        if(options.firstPayNum == 1){
                            data.fromFirstPayment = 0;
                            data.toFirstPayment = 1;
                        }else{
                            data.fromFirstPayment = 5;
                            data.toFirstPayment = null;
                        }
                    }
                }
                data.fromAge = null;
                data.toAge = null;
                if(options.others.monthPayNum == null){
                    data.fromMonthPayment = null;
                    data.toMonthPayment = null;
                }else{
                    if(options.others.monthPayNum.indexOf('-')!=-1){
                        data.fromMonthPayment = options.others.monthPayNum.split('-')[0];
                        data.toMonthPayment = options.others.monthPayNum.split('-')[1]
                    }else{
                        if(options.others.monthPayNum == 2000){
                            data.fromMonthPayment = 0;
                            data.toMonthPayment =2000;
                        }else{
                            data.fromMonthPayment = 5000;
                            data.toMonthPayment =null;
                        }
                    }
                }
                if(options.carPriceNum == null){
                    data.fromCarCost = null;
                    data.toCarCost = null;
                }else{
                    if(options.carPriceNum.indexOf('-')!=-1){
                        data.fromCarCost = options.carPriceNum.split('-')[0];
                        data.toCarCost = options.carPriceNum.split('-')[1]
                    }else{
                        if(options.carPriceNum == 10){
                            data.fromCarCost = 0;
                            data.toCarCost =10;
                        }else{
                            data.fromCarCost = 40;
                            data.toCarCost =null;
                        }
                    }
                }
                data.fromKilomiter = null;
                data.toKilomiter = null;
                data.carLevelCd =options.others.carStyleNum
            }else{
                data.pageNum = 1;  
                data.pageSize = 10;
                data.brandId = this.carId ? this.carId : null;
                data.brandSeriesId = null;
                data.fromFirstPayment = null;
                data.toFirstPayment = null;
                data.fromAge = null;
                data.toAge = null;
                data.fromMonthPayment = null;
                data.toMonthPayment = null;
                data.fromKilomiter = null;
                data.toKilomiter = null;
                data.carLevelCd =null;
                if(this.price){
                    if(this.price.indexOf('-')!=-1){
                        data.fromCarCost = this.price.split('-')[0];
                        data.toCarCost = this.price.split('-')[1]
                    }else{
                        if(this.price == 3){
                            data.fromCarCost = 0;
                            data.toCarCost =3;
                        }else{
                            data.fromCarCost = 100;
                            data.toCarCost =null;
                        }
                    }
                }else{
                     data.fromCarCost = null;
                     data.toCarCost = null;
                }
               
            }            
           this.getData(data);
        },
        getData:function(data){
            var _this = this;
            data.platForm = '020003';
             $.ajax({
                url:PC_COMMONURL+'/queryLeaseCarList',
                type:"get",
                data:data,
                dataType:'json',
                asnyc:true,
                success:function(data){
                    if(data.code == SUCCESS_CODE){
                        console.log(data.data);
                        _this.listArr = data.data.leaseCarList;
                        _this.totalCount = data.data.count;
                    }
                }
            })
        }    
    },
    components:{
        vBreadnav,
        vSearchbar,
        vList,
        vPageNation
    },
    mounted:function(){
        this.createJson(); 
    }
}
</script>



