<template>
    <section class="container">
        <v-breadnav>惠搜车</v-breadnav>
        <v-searchbar @getOption = "getselected"></v-searchbar>
        <v-list :productArr = "listArr" :type = "3"></v-list>
        <v-pageNation :totalcount = "totalCount"></v-pageNation>
    </section>
</template>

<script>
import vBreadnav from "@/components/pc/breadNav"
import vSearchbar from "@/components/pc/searchOption"
import vList from "@/components/pc/carlist"
import vPageNation from "@/components/pc/pageNation"
import {PC_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data:function(){
        return {
            listArr:[],
            totalCount:0,
        }
    },
    methods:{
        getselected:function(data){
            this.selectOption = data;
            let json = this.createJson(data);
            this.getData(json);
        },
        createJson:function(option){
            let data = new Object();
            data.pageNum = 1;
            data.pageSize = 10;
            data.brandId = option.brandId ==""? null : option.brandId ;
            data.brandSeriesId = option.carId == "" ? null : option.carId;
            if(option.firstPayNum == null){
                data.fromFirstPayment = null;
                data.toFirstPayment = null;
            }else{
                 if(option.firstPayNum.indexOf('-')!=-1){
                    data.fromFirstPayment = option.firstPayNum.split('-')[0];
                    data.toFirstPayment = option.firstPayNum.split('-')[1]
                }else{
                    if(option.firstPayNum == 1){
                        data.fromFirstPayment = 0;
                        data.toFirstPayment = 1;
                    }else{
                        data.fromFirstPayment = 5;
                        data.toFirstPayment = null;
                    }
                }
            }
            if(option.others.carAgeNum == null){
                data.fromAge = null;
                data.toAge = null;
            }else{
                if(option.others.carAgeNum.indexOf('-')!=-1){
                    data.fromAge = option.others.carAgeNum.split('-')[0];
                    data.toAge = option.others.carAgeNum.split('-')[1]
                }else{
                    if(option.others.carAgeNum == 1){
                        data.fromAge = 0;
                        data.toAge = 1;
                    }
                }
            }
            if(option.others.monthPayNum == null){
                data.fromMonthPayment = null;
                data.toMonthPayment = null;
            }else{
                if(option.others.monthPayNum.indexOf('-')!=-1){
                    data.fromMonthPayment = option.others.monthPayNum.split('-')[0];
                    data.toMonthPayment = option.others.monthPayNum.split('-')[1]
                }else{
                    if(option.others.monthPayNum == 2000){
                        data.fromMonthPayment = 0;
                        data.toMonthPayment =2000;
                    }else{
                        data.fromMonthPayment = 5000;
                        data.toMonthPayment =null;
                    }
                }
            }
            if(option.carPriceNum == null){
                data.fromCarCost = null;
                data.toCarCost = null;
            }else{
                if(option.carPriceNum.indexOf('-')!=-1){
                    data.fromCarCost = option.carPriceNum.split('-')[0];
                    data.toCarCost = option.carPriceNum.split('-')[1]
                }else{
                    if(option.carPriceNum == 10){
                        data.fromCarCost = 0;
                        data.toCarCost =10;
                    }else{
                        data.fromCarCost = 40;
                        data.toCarCost =null;
                    }
                }
            }
            if(option.others.carMileageNum == null){
                data.fromKilomiter = null;
                data.toKilomiter = null;
            }else{
                 if(option.others.carMileageNum.indexOf('-')!=-1){
                    data.fromKilomiter = option.others.carMileageNum.split('-')[0];
                    data.toKilomiter = option.others.carMileageNum.split('-')[1]
                }else{
                    if(option.others.carMileageNum == 1){
                        data.fromKilomiter = 0;
                        data.toKilomiter =1;
                    }
                }
            }  
            data.carLevelCd =option.others.carStyleNum
            return data;
        },
        getData:function(data){
            var _this = this;
            data.platForm = '020003';
             $.ajax({
                url:PC_COMMONURL+'/querySecondhandCarList',
                type:"get",
                data:data,
                dataType:'json',
                asnyc:true,
                success:function(data){
                    if(data.code == SUCCESS_CODE){
                        console.log(data.data);
                        _this.listArr = data.data.secondhandCarList;
                        _this.totalCount = data.data.count;
                    }
                }
            })
        }    
    },
    components:{
        vBreadnav,
        vSearchbar,
        vList,
        vPageNation
    },
    mounted:function(){
        let data = new Object();
        data.pageNum = 1;
        data.pageSize = 10;
        data.brandId = null;
        data.brandSeriesId = null;
        data.fromFirstPayment = null;
        data.toFirstPayment = null;
        data.fromAge = null;
        data.toAge = null;
        data.fromMonthPayment = null;
        data.toMonthPayment = null;
        data.fromKilomiter = null;
        data.toKilomiter = null;
        data.carLevelCd =null;
        data.fromCarCost = null;
        data.toCarCost = null;
        this.getData(data);
    }
}
</script>



