<template>
    <div class="container">
        <div class="row">
            <div class="col-xs-12">
                <iframe :src="descUrl" frameborder="0" width="100%"></iframe>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data:function(){
        return{
            descUrl:this.$route.params.url
        }
    }
}
</script>



