<template>
    <section class="container">
        <div class="row">
            <div class="col-xs-12">
                 <v-breadnav>平行进口</v-breadnav>
            </div>
        </div>
        <div class="row">
             <div class="col-xs-12">
                <ul class="search-option">
                    <li class="price clearfix">
                        <div class="col-xs-1">价格</div>
                        <div class="col-xs-11">
                            <a class="car-price" v-for="(item,index) in carPrice" :key="index" :data-price="item.value" @click="priceClick">
                                {{item.text}}
                            </a>
                             <div class="custom" >
                                <span>自定义：</span><input type="text" class="form-control from">--<input type="text" class="form-control to">万<button class="btn" @click="getPrice">确定</button>   
                            </div>
                        </div>
                        
                    </li>
                    <li class="category clearfix">
                        <div class="col-xs-1">品类</div>
                        <div class="col-xs-11"> 
                            <a class="car-category" v-for="(item,index) in carCategory" :key="index" :data-category="item.value" @click="categoryClick">
                                {{item.text}}
                            </a>           
                        </div>
                    </li>
                    <li class="brand clearfix">
                        <div class="col-xs-1">品牌</div>
                        <div class="col-xs-11" v-if="brandArr.length != 0">
                            <a class="car-brand" v-for="(item,index) in brandArr" :key="index" :data-id="item.id" @click="brandClick">
                                {{item.brand}}
                            </a> 
                        </div>
                    </li>
                    <li class="level clearfix">
                        <div class="col-xs-1">级别</div>
                        <div class="col-xs-11">
                            <a class="car-level" v-for="(item,index) in carLevel" :key="index" :data-level="item.value" @click="levelClick">
                                {{item.text}}
                            </a> 
                        </div>
                    </li>
                    <li class="options clearfix">
                        <div class="col-xs-1">已选</div>
                        <div class="col-xs-11 option-wrapper">
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        <v-list :productArr = "importCarList" :type= '2'></v-list> 
        <v-pageNation :totalcount = "totalCount"></v-pageNation>    
    </section>
</template>

<script>
import vBreadnav from "@/components/pc/breadNav"
import vList from "@/components/pc/carlist"
import vPageNation from "@/components/pc/pageNation"
import {PC_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data:function(){
        return{
            carPrice:[{text:"20-25万",value:"20-25"},{text:"25-35万",value:"25-35"},{text:"35-50万",value:"35-50"},{text:"50-70万",value:"50-70"},{text:"70万以上",value:"70"}],
            carCategory:[{text:"美规",value:"070001"},{text:"加规",value:"070002"},{text:"墨规",value:"070003"},{text:"中东",value:"070004"},{text:"欧规",value:"070005"}],
            carLevel:[{text:"小型车",value:"060001"},{text:"中型车",value:"060002"},{text:"SUV",value:"060003"},{text:"MPV",value:"060003"},{text:"跑车",value:"060004"}],
            brandArr:[],
            selectOption:{price:"",priceNum:"",category:"",categoryNum:"",level:"",levelNum:"",brand:"",brandId:""},
            importCarList:[],
            totalCount:0
        }
    },
    components:{
        vBreadnav,
        vList,
        vPageNation
    },
    methods:{
         brandClick:function(e){
            let brandName = e.target.innerHTML;
            let brandId = e.target.getAttribute("data-id");
            $(".car-brand").removeClass("active");
            e.target.classList.add("active");
            this.selectOption.brand = brandName;
            this.selectOption.brandId = brandId;          
            //let brandId 
        },
        priceClick:function(e){
            let priceText = e.target.innerHTML;
            let priceNum = e.target.getAttribute("data-price");
            $(".car-price").removeClass("active");
            e.target.classList.add("active");
            this.selectOption.price = priceText;
            this.selectOption.priceNum = priceNum;  
        },
        getPrice:function(){
            let from = $(".from").val();
            let to = $(".to").val();
            $(".car-price").removeClass("active");
            let priceNum = from+'-'+to;
            this.selectOption.price = priceNum+"万";
            this.selectOption.priceNum = priceNum;
        },
        categoryClick:function(e){
            let categoryText = e.target.innerHTML;
            let categoryNum = e.target.getAttribute("data-category");
            $(".car-category").removeClass("active");
            e.target.classList.add("active");
            this.selectOption.category = categoryText;
            this.selectOption.categoryNum = categoryNum;  
        },
        levelClick:function(e){
            let levelText = e.target.innerHTML;
            let levelNum = e.target.getAttribute("data-level");
            $(".car-level").removeClass("active");
            e.target.classList.add("active");
            this.selectOption.level = levelText;
            this.selectOption.levelNum = levelNum;  
        },
        selectOptionRender:function(){
            let option = this.selectOption;
            var html = "";
            if(option.brand !==""){
                html+="<a class='option-item' data-type='brand'>"+option.brand+"<span aria-hidden='true'>&times;</span></a>";
            }
            if(option.price !==""){
                html+="<a class='option-item' data-type='price'>"+option.price+"<span aria-hidden='true'>&times;</span></a>";
            }
            if(option.category!==""){
                html+="<a class='option-item' data-type='category'>"+option.category+"<span aria-hidden='true'>&times;</span></a>";
            }
             if(option.level!==""){
                html+="<a class='option-item' data-type='level'>"+option.level+"<span aria-hidden='true'>&times;</span></a>";
            }
            $(".option-wrapper").html(html);
        },
        optionFlag:function(){
            this.optionStatus = $(".option-wrapper").html() =="" ? false : true;
        },
        getSelectedOption:function(curVal,oldVal){
            console.log(curVal,oldVal);
            this.selectOptionRender();
            this.createJson(curVal);          
        },
        getListData:function(data){   
            let _this = this;       
            $.ajax({
                url:PC_COMMONURL+'/queryImportCarList',
                type:"get",
                data:data,
                dataType:"json",
                asnyc:true,
                success:function(data){
                    console.log(data);
                    if(data.code == SUCCESS_CODE){
                        _this.importCarList = data.data.importCarList;
                        _this.totalCount = data.data.count;
                    }
                }
            })
        },
        createJson:function(obj){
            let json = {};
            json.platForm = '020003';
            json.pageNum = 1;
            json.pageSize = 10;
            if(obj){
                let price = obj.priceNum.split("-");
                json.brandId = obj.brandId,
                json.brandSeriesId = null,
                json.fromPrice = price[0] ,
                json.toPrice= price[1],
                json.carClassCd = obj.categoryNum,
                json.carLevelCd = obj.levelNum 
            }else{                
                json.brandId = null,
                json.brandSeriesId = null,
                json.fromPrice = null,
                json.toPrice= null,
                json.carClassCd = null,
                json.carLevelCd = null              
            }
            this.getListData(json);  
        }

    },
    watch:{
       selectOption:{
           handler:'getSelectedOption',
           deep:true
       }
    },
    mounted:function(){
        let _this = this;
        $.ajax({
            url:PC_COMMONURL+'/queryImportCarBrandList',
            type:"get",
            data:{
                platForm :'020003'
            },
            dataType:"json",
            asnyc:true,
            success:function(data){
                if(data.code == SUCCESS_CODE){
                    _this.brandArr = data.data.brandList;
                    console.log(_this.brandArr);
                }               
            }
        })
        //初始化汽车列表
        this.createJson();         
    }
}
</script>



