<template>
    <div id="pc">
        <v-header :bgurl = "msg"></v-header>
        <v-nav></v-nav>
        <router-view/>
        <v-footer></v-footer>
    </div>
</template>

<script>
import vHeader from '@/components/pc/header';
import vNav from '@/components/pc/nav';
import vFooter from '@/components/pc/footer';
export default {
  name: 'App',
  data:function(){
    return {
      msg: "./static/images/header_01.jpg"
    }
  },
  components:{
    vHeader,
    vNav,
    vFooter
  }
}
</script>

<style lang="less">
 @import url('../../assets/css/pc-style.less');
#pc{
  min-width: 1200px;
}
</style>



